import { ProgramPageMenuViewModel } from './../../Model/ViewModel/ProgramPageMenuViewModel';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { ProgramPageViewModel } from '../../Model/ViewModel/ProgramPageViewModel';


/**
 * Generated class for the MenuModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-menu-detail-edit-modal',
  templateUrl: 'menu-detail-edit-modal.html',
})
export class MenuDetailEditModalPage {
item:ProgramPageMenuViewModel;
CanEditPermission:boolean;
title:string;
  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController) {
    this.title="順序修改";
    this.item=new ProgramPageMenuViewModel();
    this.item.page_name=navParams.data.item.page_name;
    this.item.menu_no=navParams.data.item.menu_no;
    this.item.display_order_no=navParams.data.item.display_order_no;
    this.item.creator=navParams.data.item.creator;
    this.item.create_time=navParams.data.item.create_time;
    this.item.modifier=navParams.data.item.modifier;
    this.item.last_update_time=navParams.data.item.last_update_time;
    let pm=new ProgramPageViewModel();
    pm.page_desc=navParams.data.item.ProgramPageViewModel.page_desc;
    this.item.ProgramPageViewModel=pm;
    this.CanEditPermission=navParams.data.CanEditPermission;
    // console.log(this.item);
  }

  Save() {
      this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad MenuModalPage');
  }

}



